/**
 * Objeto de estado global da aplicaÃ§Ã£o,
 * que serÃ¡ manipulado pelo usuÃ¡rio atravÃ©s dos inputs
 */
const globalState = {
  allCountries: [],
  filteredCountries: [],
  loadingData: true,
  currentFilter: "",

  radioAnd: false,
  radioOr: true,

  checkboxes: [
    {
      filter: "Java",
      description: "Java",
      checked: true
    },
    {
      filter: "JavaScript",
      description: "JavaScript",
      checked: true
    },
    {
      filter: "Python",
      description: "Python",
      checked: true
    }
  ]
};

/**
 * VariÃ¡veis globais que mapeiam elementos HTML
 */
const globalDivCountries = document.querySelector("#divCountries");
const globalInputName = document.querySelector("#inputName");
const globalDivCheckboxes = document.querySelector("#checkboxes");
const globalRadioAnd = document.querySelector("#radioAnd");
const globalRadioOr = document.querySelector("#radioOr");

/**
 * Tudo comeÃ§a aqui. A invocaÃ§Ã£o desta funÃ§Ã£o Ã© feita
 * na Ãºltima linha de cÃ³digo deste arquivo
 */
async function start() {
  /**
   * Adicionando eventos aos inputs, checkboxes e radio buttons
   */
  globalInputName.addEventListener("input", handleInputChange);
  globalRadioAnd.addEventListener("input", handleRadioClick);
  globalRadioOr.addEventListener("input", handleRadioClick);

  /**
   * Renderizando os checkboxes de forma dinÃ¢mica
   */
  renderCheckboxes();

  /**
   * Obtendo todos os paÃ­ses do backend
   * de forma assÃ­ncrona
   */
  await fetchAll();

  /**
   * Iniciamos o app jÃ¡ filtrando os paÃ­ses conforme
   * valor inicial dos inputs
   */
  filterCountries();
}

/**
 * FunÃ§Ã£o para montar os checkboxes
 * dinamicamente a partir de globalState
 */
function renderCheckboxes() {
  const { checkboxes } = globalState;

  const inputCheckboxes = checkboxes.map((checkbox) => {
    const { filter: id, description, checked } = checkbox;

    // prettier-ignore
    return (
      `<label class="option">
        <input 
          id="${id}" 
          type="checkbox" 
          checked="${checked}"
        />
        <span>${description}</span>
      </label>`
    );
  });

  globalDivCheckboxes.innerHTML = inputCheckboxes.join("");

  /**
   * Adicionando eventos
   */
  checkboxes.forEach((checkbox) => {
    const { filter: id } = checkbox;
    const element = document.querySelector(`#${id}`);
    element.addEventListener("input", handleCheckboxClick);
  });
}

/**
 * Esta funÃ§Ã£o Ã© executada somente uma vez
 * e traz todos os paÃ­ses do backend. AlÃ©m disso,
 * faz uma transformaÃ§Ã£o nos dados, incluindo um
 * campo para facilitar a pesquisa (removendo acentos,
 * espaÃ§os em branco e tornando todo o texto minÃºsculo) e
 * tambÃ©m um array contendo somente o nome das lÃ­nguas
 * faladas em determinado paÃ­s
 */
async function fetchAll() {
  const url =
    "http://localhost:3001/devs";

  const resource = await fetch(url);
  const json = await resource.json();

  const jsonWithImprovedSearch = json.map((item) => {
    const { name, picture, id } = item;

    const lowerCaseName = name.toLocaleLowerCase();

    return {
      ...item,
      searchName: removeAccentMarksFrom(lowerCaseName)
        .split("")
        .filter((char) => char !== " ")
        .join(""),
      searchLanguages: getOnlyLanguagesFrom(id)
    };
  });

  /**
   * Atribuindo valores aos campos
   * atravÃ©s de cÃ³pia
   */
  globalState.allCountries = [...jsonWithImprovedSearch];
  globalState.filteredCountries = [...jsonWithImprovedSearch];

  globalState.loadingData = false;
}

function handleInputChange({ target }) {
  /**
   * Atribuindo valor do input ao
   * globalState
   */
  globalState.currentFilter = target.value.toLocaleLowerCase().trim();

  filterCountries();
}

/**
 * Lidando com os cliques nos checkboxes
 * de forma dinÃ¢mica
 */
function handleCheckboxClick({ target }) {
  const { id, checked } = target;
  const { checkboxes } = globalState;

  /**
   * Refletindo o estado dos checkboxes
   * em globalState
   */
  const checkboxToChange = checkboxes.find(
    (checkbox) => checkbox.filter === id
  );
  checkboxToChange.checked = checked;

  filterCountries();
}

/**
 * Aqui garantimos que uma e somente uma das opÃ§Ãµes
 * de radio de state permaneÃ§a como true. Em seguida,
 * filtramos os paÃ­ses
 */
function handleRadioClick({ target }) {
  const radioId = target.id;

  globalState.radioAnd = radioId === "radioAnd";
  globalState.radioOr = radioId === "radioOr";

  filterCountries();
}

/**
 * FunÃ§Ã£o para varrer o array de idiomas
 * e trazer somente o nome em minÃºsculas, de forma ordenada
 */
function getOnlyLanguagesFrom(language) {
  return id.map((id) => id.toLocaleLowerCase()).sort();
}

/**
 * FunÃ§Ã£o para remover acentos e caracteres especiais
 * de determinado texto
 */
function removeAccentMarksFrom(text) {
  const WITH_ACCENT_MARKS = "Ã¡Ã£Ã¢Ã¤Ã Ã©Ã¨ÃªÃ«Ã­Ã¬Ã®Ã¯Ã³Ã´ÃµÃ¶Ã²ÃºÃ¹Ã»Ã¼Ã±".split("");
  const WITHOUT_ACCENT_MARKS = "aaaaaeeeeiiiiooooouuuun".split("");

  const newText = text
    .toLocaleLowerCase()
    .split("")
    .map((char) => {
      /**
       * Se indexOf retorna -1, indica
       * que o elemento nÃ£o foi encontrado
       * no array. Caso contrÃ¡rio, indexOf
       * retorna a posiÃ§Ã£o de determinado
       * caractere no array de busca
       */
      const index = WITH_ACCENT_MARKS.indexOf(char);

      /**
       * Caso o caractere acentuado tenha sido
       * encontrado, substituÃ­mos pelo equivalente
       * do array b
       */
      if (index > -1) {
        return WITHOUT_ACCENT_MARKS[index];
      }

      return char;
    })
    .join("");

  return newText;
}

/**
 * Principal funÃ§Ã£o deste app.
 *
 * Filtra os paÃ­ses conforme definiÃ§Ãµes
 * do usuÃ¡rio e invoca a renderizaÃ§Ã£o
 * da tela
 */
function filterCountries() {
  const { allCountries, radioOr, currentFilter, checkboxes } = globalState;

  /**
   * Obtendo array de idiomas a partir dos
   * checkboxes que estÃ£o marcados, retornando somente o id
   * da linguagem para facilitar a busca.
   */
  const filterCountries = checkboxes
    .filter(({ checked }) => checked)
    .map(({ filter }) => filter)
    .sort();

  /**
   * Obtendo os paÃ­ses com base nos idiomas
   * e se o usuÃ¡rio escolheu "OU", o que abrange mais opÃ§Ãµes do
   * que "E" (mais limitado)
   */
  let filteredCountries = allCountries.filter(({ searchLanguages }) => {
    /**
     * Com "OU", verificamos se pelo menos um dos idiomas
     * escolhidos pelo usuÃ¡rio pertence a determinado paÃ­s.
     * Ex: Se o usuÃ¡rio escolheu somente InglÃªs, vai retornar paÃ­se
     * que fala tanto inglÃªs quanto francÃªs, por exemplo
     *
     * Com "E", verificamos a comparaÃ§Ã£o exata do(s) idioma(s)
     * Ex: Se o usuÃ¡rio escolheu somente FrancÃªs, vai retornar paÃ­s
     * que fala somente o francÃªs
     */
    return radioOr
      ? filterCountries.some((item) => searchLanguages.includes(item))
      : filterCountries.join("") === searchLanguages.join("");
  });

  /**
   * ApÃ³s o primeiro filtro, filtramos mais uma vez
   * conforme o texto do input
   */
  if (currentFilter) {
    filteredCountries = filteredCountries.filter(({ searchName }) =>
      searchName.includes(currentFilter)
    );
  }

  /**
   * Definimos os paÃ­ses filtrados no estado do app
   * e invocamos a funÃ§Ã£o de renderizaÃ§Ã£o em seguida.
   */
  globalState.filteredCountries = filteredCountries;

  renderCountries();
}

/**
 * FunÃ§Ã£o de renderizaÃ§Ã£o dos paÃ­ses em tela
 */
function renderCountries() {
  const { filteredCountries } = globalState;

  const countriesToShow = filteredCountries
    .map((country) => {
      return renderCountry(country);
    })
    .join("");

  const renderedHTML = `
     <div>
       <h2>${filteredCountries.length} Dev(es) encontrado(s)</h2>
       <div class='row'>
         ${countriesToShow}
       </div>
     </div>
  `;

  globalDivCountries.innerHTML = renderedHTML;
}

/**
 * Isolamos a funÃ§Ã£o para renderizar um paÃ­s,
 * utilizando algumas classes do Materialize
 * e o prÃ³prio CSS do app
 */
function renderCountry(country) {
  const { name, picture, id } = country;

  return `
    <div class='col s12 m6 l4'>
      <div class='country-card'>
        <img class='flag' src="${picture}" />
        <div class='data'>
          <span>${name}</span>
          <span class='language'>
            <strong>${id}</strong>
            
          </span>
        </div>
      </div>
    </div>
  `;
}

/**
 * FunÃ§Ã£o para renderizar os idiomas.
 */
function renderLanguages(languages) {
  const { checkboxes } = globalState;
  return languages
    .map((language) => {
      return checkboxes.find((item) => item.filter === language).description;
    })
    .join(", ");
}

/**
 * Inicializando o app
 */
start();
